import React, { useContext } from 'react';
import { useNavigate } from 'react-router-dom';
import "./ecomerce.css";
import { cartecontext } from '../App';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

function Products({ item, inputdata }) {
  const { cart, setcart } = useContext(cartecontext);
  const navigate = useNavigate();

  const addtocart = (item) => {
    if (!cart.some(cartItem => cartItem.id === item.id)) {
      setcart([...cart, item]);
      toast.success('Added to cart: ' + item.title);
    }
  };

  const removecart = (item) => {
    setcart(cart.filter(cartItem => cartItem.id !== item.id));
    toast.error('Removed from cart: ' + item.title);
  };

  const filteredItems = item.filter((data) => {
    if (!data.title || !inputdata) {
      return true;
    }
    return data.title.toLowerCase().includes(inputdata.toLowerCase());
  });

  return (
    <>
      <ToastContainer />
      {filteredItems.map((item1) => (
        <div className="card" key={item1.id}>
          <div className="img">
            <img src={item1.image} alt={item1.title} />
          </div>
          <div className="title">
            <p className="text" onClick={() => navigate(`singleproduct/${item1.id}`)}>
              {item1.title.slice(0, 25)}
            </p>
          </div>
          <div className="price">
            <p className="text">Price: {item1.price}</p>
          </div>
          {cart.some(cartItem => cartItem.id === item1.id) ? (
            <button onClick={() => removecart(item1)}>Remove from Cart</button>
          ) : (
            <button onClick={() => addtocart(item1)}>Add to Cart</button>
          )}
        </div>
      ))}
    </>
  );
}

export default Products;
