import React, { createContext, useState, useEffect } from "react";
import { BrowserRouter, Routes, Route, useLocation } from "react-router-dom";
import Singleproduct from "./components/singleproduct";
import Home from "./components/Home";
import Nav from "./components/nav";
import Category from "./components/categories";
import Catagorylist from "./categorylist";
import Cart from "./components/cart";
import Login from "./loign";
import Navbar from "./components/nav";
import Footer from "./components/Footer";
export const cartecontext = createContext();

function App() {
  const [inputdata, setinputdata] = useState("");
  const location = useLocation();
  const[datafromHome,setdatafromHome]=useState(null);
  const [cart, setcart] = useState(() => {
    const savedCart = localStorage.getItem("cart");
    return savedCart ? JSON.parse(savedCart) : [];
  });

  useEffect(() => {
    localStorage.setItem("cart", JSON.stringify(cart));
  }, [cart]);

  const handleinput = (e) => {
    setinputdata(e.target.value);
  };
  const showCategoryList =
    location.pathname === "/" || location.pathname === "/product";
  const showsearchbar =
    location.pathname === "/singleproduct" || location.pathname === "/cart";
 
 const footerfunction=(data)=>{
setdatafromHome(data);
console.log("data form child"+data);

console.log(data)
 }
    return (
    <cartecontext.Provider value={{ cart, setcart }}>
      <div>
        <div className="navbar">
          <Navbar cart={cart} />
        </div>
        {/* Conditionally render search bar */}
        {location.pathname === "/" && (
          <div className="inputdata">
            <input
              type="text"
              placeholder="SearchProducts"
              onChange={(e) => handleinput(e)}
            />
          </div>
        )}
        <div className="content">
          <div className="category">
            {showCategoryList && <Catagorylist />}

            {location.pathname.startsWith("/product") && <Catagorylist />}
          </div>

          <Routes>
            <Route path="/" element={<Home inputdata={inputdata} footerfunction={footerfunction} />} />
            <Route path="/product/:category" element={<Category footerfunction={footerfunction} />} />
            <Route path="/singleproduct/:id" element={<Singleproduct />} />
            <Route path="/login" element={<Login />} />
            <Route path="/cart" element={<Cart />} />
          </Routes>
        </div>
        {/* Conditionally render footer */}
        {datafromHome && (
          <div className="footer">
            <Footer />
          </div>
        )}
      </div>
    </cartecontext.Provider>
  );
}

export default App;
