import React, { useEffect, useState } from 'react';
import { Link } from "react-router-dom";

function CategoryList() {
  const [categoryList, setCategoryList] = useState([]);
  const [isVisible, setIsVisible] = useState(false);

  const fetchCategories = async () => {
    try {
      const response = await fetch("https://fakestoreapi.com/products/categories");
      const categories = await response.json();
      setCategoryList(categories);
      setIsVisible(!isVisible); // Toggle visibility
    } catch (error) {
      console.error("Failed to fetch categories", error);
    }
  };

  // Fetch categories on mount
  useEffect(() => {
    fetchCategories();
  }, []);

  return (
    <>
      <h1 onClick={() => setIsVisible(!isVisible)} className='categoryname'>Categories</h1>
      {isVisible && <Link to="/" className='categorydata'>Show All</Link>}
      {isVisible && categoryList.map((category, index) => (
        <div className="g" key={index}>
          <Link to={`/product/${category}`} className="categorydata">
            {category}
          </Link>
        </div>
      ))}
    </>
  );
}

export default CategoryList;
