import Nav from "./nav";
import Products from "./product";
import "./ecomerce.css";
import App from "../App";
import CircularProgress from '@mui/material/CircularProgress';
import Footer from "./Footer";
import { useEffect,useState } from "react";
import { Link, useParams,useNavigate } from "react-router-dom"; 
function Home({inputdata,footerfunction}){
    const[data,setdata]=useState(null);
  const [loading, setLoading] = useState(false); // State to track loading

    const Api_Url= "https://fakestoreapi.com/products";
    const handleapi=async()=>{
        let request=await fetch(Api_Url);
        let response=await request.json();
    setdata(response);
setLoading(true)
footerfunction(response);
    }
    useEffect(()=>{
        handleapi();
    },[])
  
    return(
<>

    {!data?(


<div className='loadingbar' style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100vh' }}>
        <CircularProgress color="secondary" />
      </div> 


    ):(
<div className="paroduct">
       
<Products item={data} inputdata={inputdata} />
</div>
   
    )
}

{/* <button onClick={()=>setsearchparams({filter:"active"})}>GET SINGLE CATOGORIES</button> */}

</>
    )
}
export default Home;