import React, { useContext, useEffect, useState  } from 'react';

import CircularProgress from '@mui/material/CircularProgress';
import { useParams } from "react-router-dom";
import { cartecontext } from '../App';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import "./ecomerce.css";

const Singleproduct = () => {
  const { cart, setcart } = useContext(cartecontext);


  const addtocart = (item) => {
    if (!cart.some(cartItem => cartItem.id === item.id)) {
      setcart([...cart, item]);
      toast.success('Added to cart: ' + item.title); // Show success toast
    }
  };

  const removecart = (item) => {
    setcart(cart.filter(cartItem => cartItem.id !== item.id));
    toast.error('Removed from cart: ' + item.title); // Show error toast
  };







  const [product, setProduct] = useState(null); // Set initial state to null to avoid errors
  const params = useParams();
  const productId = params.id;

  const singleProduct = async (id) => {
    const request = await fetch(`https://fakestoreapi.com/products/${id}`);
    const response = await request.json();
    setProduct(response);
  };

  useEffect(() => {
    singleProduct(productId); // Call the function to fetch data on component mount
  }, [productId]); // Dependency array to ensure fetch happens only once
  
  return (
    <>
      < ToastContainer /> {/* Add ToastContainer to render the toast messages */}

    
      {product ? ( // Conditionally render content only if product exists
        <div className='display-product'>
          <div className='product-image'>
            <img src={product.image} alt={product.title} />
          </div>
          <div className='verticalline'>
            
          </div>
          <div className='product-details'>
            
              <p className='para'>{product.title}</p>
              <p className='price'>Price :{product.price}$</p>
              <p className='description'><span>Details</span> :{product.description}</p>
              {cart.some(cartItem => cartItem.id === product.id) ? (
            <button className=" sp-btn"onClick={() => removecart(product)}>Remove from Cart</button>
          ) : (
            <button  className=" sp-btn" onClick={() => addtocart(product)}>Add to Cart</button>
          )}

          </div>
        </div>
      ) : (
        <div className='loadingbar' style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100vh' }}>
      <CircularProgress color="secondary" />
    </div> // Display loading message while fetching data
      )}
    </>
  );
};

export default Singleproduct;
