import { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import Products from "./product";
import { BrowserRouter, Routes, Route, Link } from "react-router-dom";

function Category({footerfunction}) {





  const [category, setCategory] = useState([]);
  const params = useParams();
  let singlecategory=params.category;
  console.log(params.category);
  const getCategory = async () => {
    const request = await fetch(`https://fakestoreapi.com/products/category/${singlecategory}`);
    const response = await request.json();
    setCategory(response);
console.log(response)
footerfunction(response);

  };
  useEffect(() => {
    getCategory();
  }, [params.category]); // Call getCategory when params.category changes
  
  return (
    <>
    <div className="paroduct">
      
  
      <Products item={category} />
    </div>
    </>
  );
}

export default Category;
