import { cartecontext } from '../App';
import React, { useContext, useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
function Cart() {
 
    const { cart, setcart } = useContext(cartecontext);
    console.log(cart);
    const navigate=useNavigate();

    const[total,setotal]=useState(0);
    useEffect(()=>{
     setotal(cart.reduce((acc,curr) => acc+parseInt(curr.price),0))
    },[cart])
    return (
        <>
            {cart.map((cartitems) => (
                <div className="cart-container" key={cartitems.id}>
                    <div className="cart-img">
                        <img src={cartitems.image} alt={cartitems.title} />
                    </div>
                    <div className="cart-details">
                        <h6 onClick={()=>navigate(`/singleproduct/${cartitems.id}`)}>{cartitems.title.slice(0, 25)}</h6>
                        <p>Price: {cartitems.price}$</p>
                        <button
                            onClick={() => {
                                setcart(cart.filter(cartItem => cartItem.id !== cartitems.id));
                            }}
                        >
                            Remove from cart
                        </button>
                    </div>
                </div>
            ))}
            <div className='total-amount'>
                <p className='totalpricetext'> Total Amount : {total}$ </p>
            </div>
        </>
    );
}

export default Cart;
